*{box-sizing: border-box;}
.form{
  
  width: 100%;
  display: flex;
  flex-direction: row;
  input,button{
    width: 100%;
    border: 1px solid rgb(150, 150, 150);
    height: 30px;
    border-radius: 4px;
    outline-color: rgb(138, 138, 138);
  }
  input{
    padding: 0 15px;
    color:rgb(110, 110, 110)
  }
  button{
    padding: 0;
    margin-left: 3px;
    background-color:rgba(66, 203, 111);
    width:40px;
    color: white;
    position: relative;
    
    cursor: pointer;
    &:hover{
      background-color:rgb(148, 207, 168);
    }
    &::before{
      content:"";
      position: absolute;
      height: 3px;
      width: 20px;
      top: 13px;
      transform: translateX(-50%);
      
      background-color: white;
    }
    &::after{
      content:"";
      position: absolute;
      height: 20px;
      width: 3px;
      left: 15px;
      transform: translateX(50%);
      transform: translateY(-50%);
      background-color: white;
    }
  }
}
